main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #8c54ff;
  --secondary: #dc4eaf;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background: var(--dark);
  color: var(--light);
  font-family: "Poiret One", cursive;
}

/* fix all the hero pg and about me css to match other profile I made  */
.about-me {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}
.hero-pg {
  /* display: flex; */
  /* justify-content: center; */
  align-items: center;
  width: 100%;
  position: relative;
}
.hero-img {
  position: absolute;
}
.avatarWork {
  position: absolute;
  right: 36vw;
  top: 74px;
  width: 30vw;
  min-width: 30px;
  margin: 20px;
  margin-left: 50px;
}

