.subtitleBox {
  position: absolute;
  right: 0;
  bottom: 20px;
  width: 51vw;
  min-width: 250px;
  height: 150px;
  border: black 0.5px dashed;
  opacity: 87%;
  background: linear-gradient(to right, var(--secondary), var(--primary));
  border-right: none;
  border-top-left-radius: 12px;
  border-top-right-radius: 0;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 0;
  display: flex;
}

.subtitle {
  position: absolute;
  right: 0;
  padding-right: 2vw;
  bottom: 50px;
  font-weight: bold;
  width: 49vw;
  min-width: 250px;
  height: 80px;
  font-size: 40px;
  text-align: right;
  line-height: 100px;
}

.subtitle2 {
  position: absolute;
  right: 0;
  padding-right: 2vw;
  bottom: 30px;
  font-weight: bold;
  width: 49vw;
  min-width: 250px;
  height: 50px;
  font-size: 25px;
  text-align: right;
  line-height: 100px;
  color: var(--darker);
}
